import React from 'react'
import Card from '../Card/Card'

function Cards({algo}) {
  return (
    <div>
    {
       algo.map((e)=>(
            <div key={e.id}>
                <Card
                    name={e.name} 
                    image={e.image} 
                    genre={e.genre} 
                    id={e.id}
                />
            </div>
        ))
    }
    </div>
  )
}

export default Cards